.explorer {
  position: absolute;
}
.explorer *,
.explorer *::before,
.explorer *::after {
  box-sizing: content-box;
}
.file {
  background-color: #e8e8e8;
  border: 1px solid #a9a9a9;
  height: 140px;
  margin: 5px;
  width: 120px;
  z-index: 20;
}
.moveToTooltip {
  background-color: #fff;
  left: 103%;
  top: -192%;
  display: none;
  position: relative;
  opacity: 0.95;
  border-radius: 4px;
  color: #00f;
  white-space: nowrap;
}
.iconBorder {
  border: 2px solid #fff;
  -moz-border-radius: 40px; /* Para Firefox */
  -webkit-border-radius: 40px; /*Para Safari e Chrome */
  border-radius: 40px; /* Para Opera 10.5+*/
  background-color: #1b6eae;
  width: 80px;
  height: 80px;
}
.field {
  border: 0 dashed #808080;
  height: 150px;
  margin: 5px;
  position: absolute;
  width: 130px;
  z-index: 10;
}
.newFileHelper {
  border: 1px solid #fff;
  float: left;
  height: 140px;
  margin: 5px -129px 5px 4px;
  width: 120px;
}
.inputFileName {
  background-color: #e8e8e8;
  border: medium none;
  color: #000;
  cursor: default;
  height: 40px;
  width: 117px;
}
.contextMenu,
.contextMenuFile,
.contextMenuFolder,
.contextMenuVoid {
  background-color: #fff;
  border: 2px solid #808080;
  height: auto;
  width: auto;
  min-width: 130px;
  max-width: 180px;
  z-index: 9999;
}
.contextMenuFile {
  position: relative;
}
.contextMenuFolder {
  position: relative;
}
.contextMenuVoid {
  position: absolute;
}
.baseDialog {
  background-color: #fff;
  border: 2px solid #808080;
  display: none;
  overflow-x: hidden;
  overflow-y: auto;
  position: absolute;
  z-index: 9999;
}
.baseDialogContent {
  padding-top: 20px !important;
  padding-bottom: 20px !important;
}
._selected {
  background-color: #00f;
  box-sizing: inherit;
  height: 140px;
  top: -126px;
  position: relative;
  width: 120px;
}
.contextMenuOption {
  padding: 10px !important;
}
.contextMenuOption:hover {
  background-color: #808080;
  color: #fff;
}
.disabledContextMenuOption {
  background-color: #fff !important;
  color: #c8c8c8 !important;
  cursor: default !important;
  font-style: italic !important;
}
.explorerButton {
  background-color: #1b58b8;
  border: 3px solid #fff;
  color: #fff;
  font-weight: bold;
  height: 45px;
  padding: 10px;
  text-align: center;
  width: 170px;
}
.explorerButton:hover {
  opacity: 0.7;
}
.explorerButtonDisabled {
  background-color: #a9a9a9 !important;
  cursor: default;
}
.explorerButtonDisabled:hover {
  background-color: #a9a9a9 !important;
  opacity: 1 !important;
}
.explorerInput {
  height: 30px;
}
.onLeft {
  float: left;
}
.onRight {
  float: right;
}
.transferEffect {
  border: 1px solid #808080;
}
.handCursor {
  cursor: pointer;
}
.bold {
  font-weight: bold;
}
.opacity4 {
  opacity: 0.4;
}
.opacity6 {
  opacity: 0.6;
}
.opacity8 {
  opacity: 0.8;
}
.opacity9 {
  opacity: 0.9;
}
.opacity98 {
  opacity: 0.98;
}
.gray {
  color: #808080;
}
.white {
  color: #fff;
}
.ft8 {
  font-size: 8pt;
}
.ft10 {
  font-size: 10pt;
}
.ft11 {
  font-size: 11pt;
}
.ft12 {
  font-size: 12pt;
}
.ft14 {
  font-size: 14pt;
}
.ft16 {
  font-size: 16pt;
}
.ft18 {
  font-size: 18pt;
}
.ft30 {
  font-size: 30pt;
}
.ft40 {
  font-size: 40pt;
}
.ft45 {
  font-size: 45pt;
}
.ft50 {
  font-size: 50pt;
}
.txtcenter {
  text-align: center;
}
.txtjustify {
  text-align: justify;
}
.txtleft {
  text-align: left;
}
.txtmargin {
  padding: 2px 7px;
}
.displayNone {
  display: none;
}
.center {
  margin: 0 auto !important;
}
.radius$radius {
  border-radius: 10px;
}
.radius$radius {
  border-radius: 20px;
}
.closeBaseDialog {
  background: rgba(0,0,0,0) url("../icons/close-base-dialog.png") no-repeat scroll 0 0;
  height: 32px;
  position: relative;
  width: 32px;
  z-index: 9999;
}
.closeBaseDialog:hover {
  opacity: 0.4;
}
.quickAccessLink {
  color: #23649d;
}
.quickAccessLink:hover {
  background-color: #23649d;
  color: #fff;
}
